
declare var Swiper : any;
declare var Parallax : any;
declare var gsap : any;
declare var ScrollTrigger : any;
gsap.registerPlugin(ScrollTrigger)

function indexPage (){
  const index = document.getElementById("index-Page")
  if(index){
    var scene_1 = document.getElementById('Scene_1');
    var parallaxInstance = new Parallax(scene_1, {
      relativeInput: true
    });
    var scene_2 = document.getElementById('Scene_2');
    var parallaxInstance = new Parallax(scene_2, {
      relativeInput: true
    });
    parallaxInstance.friction(.2, .2);
  }
}




// handle click link button
const onscrollHeader = () => {
	var body = document.getElementsByTagName("body")[0] ;
  // console.log(body)
	window.onscroll = function () { scrollFunction() };

	function scrollFunction() {
		if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
			body.classList.add("white");
		} else {
			body.classList.remove("white");
		}
	}
}

const hamburgerHover = () => {
	var hamburger = document.querySelector("#header .header-wrapper__nav") ;
  if(hamburger){
    hamburger.addEventListener("mouseover",()=>{
      var hamburgerHover = hamburger.querySelector(".header-wrapper__nav-title") as HTMLElement
      hamburgerHover.style.animation = "flowRight .3s 1 ease-in-out forwards"
    })
    hamburger.addEventListener("mouseout",()=>{
      var hamburgerHover = hamburger.querySelector(".header-wrapper__nav-title") as HTMLElement
      hamburgerHover.style.animation = "flowRight_Reverse .3s 1 ease-in-out forwards"
    })
  }
	
}

const index_1 =()=>{
  const i_1 =  document.querySelector("#dp-information")
  if(i_1){
    const items = document.querySelectorAll("#dp-information .information__wrapper--list_items")
  
  
    items.forEach(function (element){
    gsap.from(element,{
      scrollTrigger:element,
      opacity:0,
      duration:1.3,
      x: 200
    })
  })
  gsap.from(".information .information__wrapper--title",{
    scrollTrigger:".information .information__wrapper--title",
    opacity:0,
    duration:1,
    y: 100
  })
  }
  
}

const index_2 =()=>{
  const i_2 =  document.querySelector("#dp-soft-skill")
  if(i_2){
    gsap.from(".soft-skill .soft-skill__wrapper--title",{
      scrollTrigger:".soft-skill .soft-skill__wrapper--title",
      opacity:0,
      duration:1,
      y: 100
    })
    gsap.from(".soft-skill .Scene_1",{
      scrollTrigger:".soft-skill .Scene_1",
      opacity:0,
      duration:.7,
      y: 100
    })
    gsap.from(".soft-skill .Scene_2",{
      scrollTrigger:".soft-skill .Scene_2",
      opacity:0,
      duration:.8,
      delay:.5,
      y: 100
    })
    gsap.from(".soft-skill .Scene_3",{
      scrollTrigger:".soft-skill .Scene_3",
      opacity:0,
      duration:.9,
      delay:1,
      y: 100
    })
    gsap.from(".soft-skill .soft-skill__wrapper--list-img",{
      scrollTrigger:".soft-skill .soft-skill__wrapper--list-img",
      opacity:0,
      duration:.9,
      x: 200
    })
  }
}

