declare var gsap : any;
declare var Parallax : any;
declare var Swiper : any;
declare var ScrollTrigger : any;
gsap.registerPlugin(ScrollTrigger)

// const Parallax = require('parallax-js')
function activeProcess (){
    const processPage = document.getElementById("dp-soft-skill")
    if(processPage){
        const listName = processPage.querySelectorAll("ol .items")
        listName.forEach(element => element.addEventListener('click',()=>{
            const listNameACtive = processPage.querySelector("ol .items.active")
            const listInforACtive = processPage.querySelector("ol .items-infor.active")
            element.classList.toggle("active")
            const listInfor = element.querySelector(".items-infor")
            // if(element.classList.value === "items active"){
            //     if(listInfor){
            //         listInfor.classList.add("active")
            //     }
                
            // }
            // else{
            //     if(listInfor){
            //         listInfor.classList.remove("active")
            //     }
            // }
            if( listInforACtive){
                listNameACtive.classList.remove("active")
            }
            
        }))
        const listNameACtive = processPage.querySelector("ol .items.active")
    }
}   

const process_1 =()=>{
    const p_1 =  document.querySelector("#dp-progress")
    if(p_1){
        gsap.from(".progress .progress__wrapper--title",{
            scrollTrigger:".progress .progress__wrapper--title",
            opacity:0,
            duration:1,
            y: 100
        })
        gsap.from(".progress .Scene_1",{
            scrollTrigger:".progress .Scene_1",
            opacity:0,
            duration:.7,
            y: 100
        })
        gsap.from(".progress .Scene_2",{
            scrollTrigger:".progress .Scene_2",
            opacity:0,
            duration:.8,
            delay:.5,
            y: 100
        })
        gsap.from(".progress .Scene_3",{
            scrollTrigger:".progress .Scene_3",
            opacity:0,
            duration:.9,
            delay:1,
            y: 100
        })
        gsap.from(".progress .progress__wrapper--list-img",{
            scrollTrigger:".progress .progress__wrapper--list-img",
            opacity:0,
            duration:.9,
            x: 200
        })
    }
}
